.register{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        to bottom,
     rgb(0,0,0,0) 0%,
     rgba(0,0,0,1) 100%
      ) , url("https://i.ibb.co/TW47s2T/Netflix-Background.jpg");
    background-size: cover;
    position: relative;
   .top{
        .wrapper{
             padding: 20px 50px;
             display: flex;
             align-items: center;
             justify-content: space-between;
             .logo{
                height: 80px;
            }
            .loginButton{
                z-index: 999;
                cursor: pointer;
                background-color: red;
                border: none;
                color: white;
                border-radius: 5px;
                padding: 5px 15px;
                font-size: 16px;
                font-weight: 600;
            }
        }
        
   } 
   .container{
     width: 100%;
     height: 100%;
     position: absolute;
     top: 0;
     right: 0;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     color: white;

     h1{
        font-size: 50px;
     }
     h2{
        margin: 20px;
     }
     p{
        font-size: 20px;
     }
     .input{
         width: 50%;
         background-color: white;
         display: flex;
         align-items: center;
         justify-content: space-between;
         margin-top: 20px;
         height: 50px;
         
          background-color: var(--main-color);
         input{
            border-radius: 2px;
            flex:9;
            height: 100%;
            border: none;
            padding: 0 10px;
         }
         .registerButton{
            flex: 3;
            border-radius: 2px;
            margin-left: 0.5rem;
            height: 100%;
            background-color: red;
            border: none;
            color: white;
            font-size: 20px;
            font-weight: 600;
            cursor: pointer;
         }
     }
   }
}